import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'

function load(component) {
  return () => import(`@/views/${component}.vue`)
}

function cat(component) {
  return () => import(`@/categories/${component}.vue`)
}

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/main',
    name: 'main',
    component: load('Main')
  },
  {
    path: '/checkout',
    name: 'checkout',
    component: load('Checkout')
  },
  {
    path: '/cart',
    name: 'cart',
    component: load('Cart')
  },
  {
    path: '/:catchAll(.*)*',
    name: 'notfound',
    component: load('404')
  },
  {
    path: '/biography',
    name: 'biography',
    component: cat('Biography')
  },
  {
    path: '/business',
    name: 'business',
    component: cat('Business')
  },
  {
    path: '/childerens',
    name: 'childerens',
    component: cat('Childerens')
  },
  {
    path: '/fantasy',
    name: 'fantasy',
    component: cat('Fantasy')
  },
  {
    path: '/politics',
    name: 'politics',
    component: cat('Politics')
  },
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router