<template>
  <div class="products">
    <button class="products__backTopBtn" @click="goBack">Назад</button>
    <div class="products__wrap">
      <img :src="convertImageUrl(product.image.url)" alt="" class="products__img" />
      <div class="products__desc">
        <h1 class="products__h">{{ product.name }}</h1>
        <div class="products__p" v-html="product.description"></div>
        <p class="products__price">{{ product.price.formatted }}0 Сум</p>
        <button
          class="products__btn"
          @click="addToCart({ productId: product.id, quantity: 1 })"
          :disabled="addingToCart === product.id"
        >
          <span v-if="addingToCart !== product.id"> ДОБАВИТЬ В КОРЗИНУ </span>
          <div v-else class="loader">
            <span class="loader__element"></span>
            <span class="loader__element"></span>
            <span class="loader__element"></span>
          </div>
        </button>
        <button class="products__back" @click="goBack">
          <img src="@/assets/img/back.svg" alt="" class="products__back-img" />
          Назад
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  props: ["product"],
  computed: {
    ...mapState(["addingToCart"]),
  },
  methods: {
    ...mapActions(["addToCart", "clearSelectedProduct"]),
    goBack() {
      this.clearSelectedProduct();
    },
    convertImageUrl(url) {
      return url.replace(/\|/g, '%7C');
    },
  },
};
</script>

<style>
.loader {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;
}

.loader__element {
  border-radius: 100%;
  border: 5px solid white;
  margin: calc(5px * 2);
}

.loader__element:nth-child(1) {
  animation: preloader 0.6s ease-in-out alternate infinite;
}
.loader__element:nth-child(2) {
  animation: preloader 0.6s ease-in-out alternate 0.2s infinite;
}

.loader__element:nth-child(3) {
  animation: preloader 0.6s ease-in-out alternate 0.4s infinite;
}

@keyframes preloader {
  100% {
    transform: scale(2);
  }
}
</style>
