<template>
  <header class="header">
    <div class="container">
      <div class="header__wrap">
        <router-link to="/" class="header__logo">
          <img src="@/assets/img/logo.png" alt="" class="header__logo-img" />
        </router-link>
        <div class="header__search">
          <Transition name="input">
            <input
              type="text"
              class="header__search-input"
              placeholder="Поиск..."
              v-model="search"
              v-if="header == true"
              @input="performSearch"
            />
          </Transition>
        </div>
        <ul class="header__menu">
          <li>
            <button class="header__menu-search" @click="changeHeader">
              <img
                src="@/assets/img/search.svg"
                alt=""
                v-if="header == false"
              />
              <img src="@/assets/img/search-none.svg" alt="" v-else />
            </button>
          </li>
          <li>
            <button
              class="header__categories"
              @click="
                category == false ? (category = true, header = false) : (category = false)
              "
            >
              <img
                src="@/assets/img/categories-icon.png"
                alt=""
                class="header__categories-img"
              />
            </button>
          </li>
          <li>
            <a href="tel:+998991234567"
              ><img
                class="header__link-phone"
                src="@/assets/img/phone-icon.svg"
                alt=""
            /></a>
          </li>
          <li>
            <router-link to="/main" class="header__link-p">Магазин</router-link>
          </li>
          <li class="header__menu-li">
            <router-link to="/cart" class="header__link">
              <img
                class="header__link-img"
                src="@/assets/img/cart.svg"
                alt=""
              />
              <span class="header__link-img-span">{{
                cart ? cart.total_items : 0
              }}</span>
            </router-link>
          </li>
        </ul>
      </div>
      <Transition name="categoryAnim">
        <div class="category" v-show="category">
          <div class="category__block"></div>
          <router-link
            to="/biography"
            class="category__link"
            @click="categoryOff"
            >Биография</router-link
          >
          <router-link
            to="/business"
            class="category__link"
            @click="categoryOff"
            >Бизнес</router-link
          >
          <router-link
            to="/childerens"
            class="category__link"
            @click="categoryOff"
            >Детский</router-link
          >
          <router-link to="/fantasy" class="category__link" @click="categoryOff"
            >Фантастические</router-link
          >
          <router-link
            to="/politics"
            class="category__link"
            @click="categoryOff"
            >Политика</router-link
          >
        </div>
      </Transition>
    </div>
  </header>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      search: "",
      header: false,
      category: false,
    };
  },
  computed: {
    ...mapState(["cart"]),
  },
  methods: {
    changeHeader() {
      this.header = !this.header;
      this.search = "";
      this.$store.commit("setSearchResults", null);
      this.category = false
    },
    async performSearch() {
      if (this.search != "") {
        await this.$store.dispatch("searchProducts", this.search);
      } else {
        this.$store.commit("setSearchResults", null);
      }
    },
    categoryOff() {
      this.category = false;
    },
  },
};
</script>

<style scoped>
.input-enter-active,
.input-leave-active {
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

.input-enter-from,
.input-leave-to {
  transform: translateX(100%);
  opacity: 0;
}

.categoryAnim-enter-active,
.categoryAnim-leave-active {
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

.categoryAnim-enter-from,
.categoryAnim-leave-to {
  transform: translateY(-100%);
  opacity: 0;
}
</style>
