import { createStore } from 'vuex'
import { commerce } from '@/lib/commerce';
export default createStore({
  state: {
    products: null,
    latestProducts:null,
    cart: null,
    addingToCart: null,
    selectedProduct: null,
    loading: false,
    searchResults: null,
    categories: [],
    productsByCategory: {
      biography: [],
      business: [],
      childerens: [],
      fantasy: [],
      politics: [],
    },
  },
  getters: {
  },
  mutations: {
    getProducts(state, payload) {
      state.products = payload;
      console.log(state.products);
    },
    getLatestProducts(state, payload) {
      state.latestProducts = payload;
      console.log(state.latestProducts);
    },
    getCart(state, payload) {
      state.cart = payload;
      console.log(state.cart);
    },
    updateCart(state, payload) {
      state.cart = payload;
      console.log(state.cart);
    },
    setAddingToCart(state, productId) {
      state.addingToCart = productId;
    },
    setSelectedProduct(state, product) {
      state.selectedProduct = product;
    },
    clearSelectedProduct(state) {
      state.selectedProduct = null;
    },
    setLoading(state, loading) {
      state.loading = loading;
    },
    setSearchResults(state, results) {
      state.searchResults = results;
    },
    productsByCategory(state, { categoryId, item }) {
      state.productsByCategory[categoryId] = item;
    },
  },
  actions: {
    async getProducts({ commit }) {
      try {
        const { data } = await commerce.products.list();
        commit('getProducts', data);
      } catch (error) {
        console.error('Error fetching products:', error);
      }
    },
    async getLatestProducts({ commit }) {
      try {
        const { data } = await commerce.products.list();
        commit('getLatestProducts', data.slice(-4));
      } catch (error) {
        console.error('Error fetching products:', error);
      }
    },
    async getCart({ commit }) {
      try {
        const cart = await commerce.cart.retrieve();
        commit('getCart', cart);
      } catch (error) {
        console.error('Error fetching cart:', error);
      }
    },
    async addToCart({ dispatch, commit }, { productId, quantity }) {
      try {
        commit('setAddingToCart', productId);
        const response = await commerce.cart.add(productId, quantity);
        await dispatch('updateCart');
      } catch (error) {
        console.error('Error adding to cart:', error);
      } finally {
        commit('setAddingToCart', null);
      }
    },
    async updateCart({ commit }) {
      try {
        const cart = await commerce.cart.retrieve();
        commit('updateCart', cart);
      } catch (error) {
        console.error('Error updating cart:', error);
      }
    },
    async clearCart({ dispatch, commit }) {
      try {
        commit('setLoading', true);
        await commerce.cart.empty();
        await dispatch("updateCart");
      } catch (error) {
        console.error("Error clearing cart:", error);
      } finally {
        commit('setLoading', false);
      }
    },
    async searchProducts({ commit, state }, searchQuery) {
      try {
        if (!state.products) {
          await this.dispatch("getProducts");
        }
        const filteredProducts = state.products.filter(product => {
          return product.name.toLowerCase().includes(searchQuery.toLowerCase());
        });
        commit('setSearchResults', filteredProducts);
      } catch (error) {
        console.error('Error searching products:', error);
      }
    },
    async fetchProductsByCategory({ commit }, { categoryId }) {
      commit('setLoading', true);
      try {
        const { data: item } = await commerce.products.list({
          category_slug: categoryId,
        });
        commit('productsByCategory', { categoryId, item });
      } catch (error) {
        console.error('Error fetching products by category:', error);
      } finally {
        commit('setLoading', false);
      }
    },
    selectProduct({ commit }, product) {
      commit('setSelectedProduct', product);
    },
    clearSelectedProduct({ commit }) {
      commit('clearSelectedProduct');
    },
  },
  modules: {
  }
})